html, body, div, a, img, header, nav, ul, li {
  --header-height: 60px;
  --moyo-blue-color: #00acdc;
  margin: 0;
  padding: 0;
}

.html {
  font-family: Roboto, sans-serif;
}

.main {
  height: calc(100vh - var(--header-height));
  background-color: #bbb;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo_url {
  width: 40px;
  height: 40px;
}

.nav {
  height: var(--header-height);
  display: flex;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__url {
  height: var(--header-height);
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: var(--header-height);
  margin: 0 10px;
  text-decoration: none;
  display: block;
}

.nav__li:first-child a.nav__url {
  margin-left: 0;
}

.nav__li:last-child a.nav__url {
  margin-right: 0;
}

.nav__url:hover, .is-active {
  color: var(--moyo-blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--moyo-blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}
